var time = require('../../../utils/getDate.js');

export default {
	data() {
		return {
			columns: ['男', '女'],
			maxDate: new Date().getTime(),
			studentBirth: '',
			studentGender: '',
			formatter(type, value) {
				if (type === 'year') {
					return `${value}年`;
				} else if (type === 'month') {
					return `${value}月`;
				} else if (type === 'day') {
					return `${value}日`;
				}
				return value;
			},
			getUserInfoList: [],
			showSex: false,
			showBir: false,
			showTx: false,
			picSrc: '',
			Areaaddress: '', //当前位置
			userId: ""
		}
	},
	components: {
		// popupLayer,
	},
	methods: {
		// 返回
		goBack() {
			uni.navigateBack({
				delta: 1
			});
		},
		// 头像弹框
		show(type) {
			this.showTx = true
		},
		close(type) {
			this.showTx = false
		},
		// 性别弹框
		show1() {
			this.showSex = true
		},
		close1() {
			this.showSex = false
		},

		onConfirm(event) {
			// console.log(`当前值：${value}, 当前索引：${index}`)
			this.close1()
			const {
				picker,
				value,
				index
			} = event.detail;
			console.log(`当前值：${value}, 当前索引：${index}`);
			this.studentGender = value
			this.$minApi.updatetUserInfo({
				userIdenty: 0,
				id: this.userId,
				gender: index
			}).then(data => {
				if (data.code == 200) {
					uni.showToast({
						title: "修改成功！",
						icon: "none"
					})
				} else {
					uni.showToast({
						title: data.msg,
						icon: "none"
					})
				}
				console.log(data)
				this.getUserInfo()
			})
			// console.log(data)
			
			this.getUserInfo()
		},

		onCancel() {
			console.log('取消');
			this.close1()
		},

		// 生日弹框
		show2() {
			this.showBir = true
		},
		close2() {
			this.showBir = false
		},
		birInput(val) {
			// console.log(val)
		},
		birConfirm(val) {
			console.log(val)
			// this.setData({
			// 	currentDate: val.detail
			// });
			this.studentBirth = time.formatTime(new Date(val.detail), 'Y-M-D')
			console.log(this.teacherBirth)
			// var a=time.formatTimeTwo(this.birthday)				
			this.close2()
			
			
			this.$minApi.updatetUserInfo({
				userIdenty: 0,
				id: this.userId,
				birthday: this.studentBirth
			}).then(data => {
				if (data.code == 200) {
					uni.showToast({
						title: "修改成功！",
						icon: "none"
					})
				} else {
					uni.showToast({
						title: data.msg,
						icon: "none"
					})
				}
				console.log(data)
				// this.getUserInfo()
			})
			
			
		},
		birCancel() {
			this.close2()
		},
		// 获取用户信息
		getUserInfo() {
			this.$minApi.getUserInfo({}).then(data => {
				// console.log(data)
				this.getUserInfoList = data.data
				this.studentBirth = data.data.studentBirth
				this.studentGender = data.data.studentGender
				this.userId = data.data.studentId
			})
		},
		// 从本地相册选择图片或使用相机拍照。
		selectPic(type) {
		let this_ = this
		uni.chooseImage({
			count: 1,
			sourceType: [type],
			success: (res) => {
				console.log(res)
				this_.close('headPic')
				this_.picSrc = res.tempFilePaths[0]
		
				const tempFilePaths = res.tempFilePaths;
				const token = uni.getStorageSync('token');
				uni.uploadFile({
					url: 'http://148.70.55.201:8089/backwordSystem/uploadFile',
					filePath: tempFilePaths[0],
					fileType: "image",
					name: 'file',
					header: {
						'content-type': 'multipart/form-data',
						'X-Token': token,
					},
					formData: {
						userId: this.userId,
						userIdenty: "0"
					},
					success: (uploadFileRes) => {
						console.log(uploadFileRes);
						console.log(uploadFileRes.data.filePath);
						if (uploadFileRes.statusCode == 200) {
							this.userImg = "http://148.70.55.201:8089/backwordSystem" + uploadFileRes.data.filePath
							this.$minApi.updatetUserInfo({
								userIdenty: "0",
								id: this.userId,
								avatarPath: this.userImg
							}).then(data => {
								console.log(data)
								// this.getUserInfo()
							})
						} else {
							console.log("ddd")
							uni.showToast({
								title: "修改失败，请稍后再试！",
								icon: "none"
							})
						}
		
					}
				});
			},
		
		})
					
		}
	},
	created() {
		this.birthday = time.formatTime(new Date(this.birthday), 'Y-M-D')
		this.getUserInfo()
	}
}
